import React, { useState } from 'react';
import { ec as EC } from 'elliptic';
import bigInt from 'big-integer';

const ec = new EC('curve25519');

const ECDHKeyExchangeSimulation = () => {
  const [curve, setCurve] = useState({ a: 486662, b: 1, p: bigInt(2).pow(255).subtract(19) });
  const [privateKeyA, setPrivateKeyA] = useState(null);
  const [publicKeyA, setPublicKeyA] = useState(null);
  const [privateKeyB, setPrivateKeyB] = useState(null);
  const [publicKeyB, setPublicKeyB] = useState(null);
  const [secretKeyA, setSecretKeyA] = useState(null);
  const [secretKeyB, setSecretKeyB] = useState(null);

  const generatePointG = async () => {
    try {
      // Generate a secure random private key using the browser's crypto API
      const privateKeyA = bigInt.randBetween(1, curve.p);

      // Convert the private key to elliptic format
      const ellipticPrivateKeyA = ec.keyFromPrivate(privateKeyA.toString());

      // Create a new key pair using elliptic for User A
      const keyPairA = ellipticPrivateKeyA.getPublic();

      setPrivateKeyA(privateKeyA);
      setPublicKeyA(keyPairA);

      // User B generates its own key pair
      const privateKeyB = bigInt.randBetween(1, curve.p);
      const ellipticPrivateKeyB = ec.keyFromPrivate(privateKeyB.toString());
      const keyPairB = ellipticPrivateKeyB.getPublic();

      setPrivateKeyB(privateKeyB);
      setPublicKeyB(keyPairB);

      // Calculate secret keys directly
      const secretKeyA = ellipticPrivateKeyB.derive(ellipticPrivateKeyA.getPublic()).toArray();
      const secretKeyB = ellipticPrivateKeyA.derive(keyPairB).toArray();

      setSecretKeyA(secretKeyA);
      setSecretKeyB(secretKeyB);
    } catch (error) {
      console.error('Error generating key pair:', error);
    }
  };

  const handleSimulation = async () => {
    await generatePointG();
  };

  return (
    <div className="container mx-auto mt-8 flex">
      <div className="flex-shrink-0 w-1/4 mr-4">
        <div className="bg-white p-4 rounded-md shadow-md">
          <p className="mb-2">
            <strong>Explanation</strong>
          </p>
          <p>
            In Elliptic Curve Diffie-Hellman (ECDH), the base point (G) is a fixed point on the curve. For Curve25519, its
            x-coordinate is 9. The y-coordinate is calculated based on the curve equation.
          </p>
          <p className="mt-4">
            The provided values <code>000000000000000000000000000<br/>0000000000000000000000000000000000009</code> are a common
            representation of the x-coordinate of the base point (G).
          </p>
          <br/>
          <p>
            In ECDH, secret keys are calculated by multiplying a private key with the public key of the other user.
            This shared secret key can be used for secure communication.
          </p>
        </div>
      </div>
    
      <div className="flex-grow">
        <div className="p-4 bg-gray-100 rounded-md shadow-md">
          <h1 className="text-2xl font-bold mb-4 mt-10">ECDH Key Exchange Simulation</h1>

          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            onClick={handleSimulation}
          >
            Start Simulation
          </button>

          {privateKeyA && (
            <div className="mt-8 p-4 bg-white rounded-md shadow-md">
              <p>
                <strong>Step 1: Elliptic Curve Parameters</strong>
              </p>
              <p>a: {curve.a}, b: {curve.b},<br/> p: {curve.p.toString()} (2<sup>255</sup>-19 hence the name Curve25519)</p>

          <p className="mt-4">
            <strong>Step 2: User A</strong>
          </p>
          <p className="mb-2">1. Generate a secure random private key (nA) using the browser's crypto API.</p>
          <p className="mb-2">2. Convert the private key to elliptic format.</p>
          <p className="mb-2">3. Calculate the public key (PA) by multiplying the private key with the curve's base point (G).</p>
          <p className="mb-2">
            Private Key (nA): {privateKeyA.toString()}
            <br />
            Public Key (PA): {publicKeyA ? publicKeyA.getX().toString() : 'Calculating...'}
          </p>

          <p className="mt-4">
            <strong>Step 3: User B</strong>
          </p>
          <p className="mb-2">1. User B generates its own secure random private key (nB).</p>
          <p className="mb-2">2. User B calculates the public key (PB) by multiplying the private key with the curve's base point (G).</p>
          <p className="mb-2">
            Private Key (nB): {privateKeyB.toString()}
            <br />
            Public Key (PB): {publicKeyB ? publicKeyB.getX().toString() : 'Calculating...'}
          </p>

          <p className="mt-4">
                <strong>Step 4: Secret Key of User A</strong>
              </p>
              <p>
                Secret Key (K) for User A: {secretKeyA ? secretKeyA : 'Calculating...'}
              </p>

              <p className="mt-4">
                <strong>Step 5: Secret Key of User B</strong>
              </p>
              <p>
                Secret Key (K) for User B: {secretKeyB ? secretKeyB : 'Calculating...'}
              </p>
            </div>
          )}

          <div className="mt-8 p-4 bg-white rounded-md shadow-md">
          <p className="mb-2">
              <strong>Elliptic Curve Base Point (G)</strong>
            </p>
            <p>
              G (hex): {ec.g.encodeCompressed('hex')}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ECDHKeyExchangeSimulation;
